import { getPosts } from '@/api/posts'

export default {
  layout: 'HeroPage',
  async asyncData() {
    const tag = 'gathering'
    const posts = await getPosts(tag)
    return { posts }
  },
  head() {
    return {
      title: 'Our scheduled gatherings',
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: 'Gathering list'
        }
      ]
    }
  }
}
